import React, { useEffect, useState } from "react";
import { API_KEY, API_URL } from "../config";

import Preloader from "./Preloader";
import GoodsList from "./GoodsList";
import Cart from "./Basket/Cart";
import BasketList from "./Basket/BasketList";
import Alert from "./Basket/Alert";

const Shop = () => {
  const [ goods, setGoods ] = useState([]);
  const [ loading, setLoading ] = useState(true);
  const [ order, setOrder ] = useState([]);
  const [ isBasketShow, setBasketShow ] = useState(false);
  const [ alertName,  setAlertName ] = useState('');
  // console.log(order);


  const addToBasket = (item) => { 
    const itemIndex = order.findIndex(
        (orderItem) => orderItem.id === item.id
    );

    if (itemIndex < 0) {
        const newItem = {
            ...item,
            quantity: 1,
        };
        setOrder([...order, newItem]);
    } else {
        const newOrder = order.map((orderItem, index) => {
            if (index === itemIndex) {
                return {
                    ...orderItem,
                    quantity: orderItem.quantity + 1,
                };
            } else {
                return orderItem;
            }
        });

        setOrder(newOrder);
    }
    setAlertName(item.name);
};

const removeFromBasket = (itemId) => {
  const newOrder = order.filter(el => el.id !== itemId);
  setOrder(newOrder);
};

const incQuantity = (itemId) => {
  const newOrder = order.map((el) => {
    if (el.id === itemId) {
      const newQuantity = el.quantity + 1;
      return {
        ...el,
        quantity: newQuantity
      }
    } else {
      return el;
    }
  });
  setOrder(newOrder);
};

const decQuantity = (itemId) => {
  const newOrder = order.map((el) => {
    if (el.id === itemId) {
      const newQuantity = el.quantity - 1;
      return {
        ...el,
        quantity: newQuantity >= 0 ? newQuantity : 0
      }
    } else {
      return el;
    }
  });
  setOrder(newOrder);
};

const handleBasketShow = () => {
  setBasketShow(!isBasketShow);
};

const closeAlert = () => {
  setAlertName('')
};

  const getApi = async () => {
    const response = await fetch(API_URL, {
      headers: {
            //Ð²Ð½ÑÑÑÐ¸ ÐºÐ»ÑÑ ÑÐµÐ´ÐµÑÑ, Ð´Ð»Ñ Ð¾Ð±ÑÐ°ÑÐµÐ½Ð¸Ñ Ðº API
            Authorization: API_KEY, // ÐºÐ»ÑÑ Ð¸ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ ÑÐµÐ´ÐµÑÑ
    }});
    const data = await response.json();
    console.log(data.featured); // Ð´Ð¾ÑÑÑÑÐ°ÑÑÑÑ Ð´Ð¾ Ð¼Ð°ÑÑÐ¸Ð²Ð°
    setGoods(data.featured);
    setLoading(false);
   };

  useEffect(() => {
    getApi();
    // fetch(API_URL, {
    //   headers: {
    //     //Ð²Ð½ÑÑÑÐ¸ ÐºÐ»ÑÑ ÑÐµÐ´ÐµÑÑ, Ð´Ð»Ñ Ð¾Ð±ÑÐ°ÑÐµÐ½Ð¸Ñ Ðº API
    //     Authorization: API_KEY, // ÐºÐ»ÑÑ Ð¸ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ ÑÐµÐ´ÐµÑÑ
    //   },
    // })
    //   .then((response) => response.json()) // Ð¿Ð¾Ð»ÑÑÐµÐ½Ð½ÑÐ¹ Ð¾ÑÐ²ÐµÑ Ð¿ÑÐµÐ¾Ð±ÑÐ°Ð·ÑÐ¹ Ð² json
    //   .then((data) => {
    //     // Ð¿Ð¾Ð»ÑÑÐµÐ½Ð½ÑÐµ Ð´Ð°Ð½Ð½ÑÐµ
    //     data.featured && setGoods(data.featured); // Ð¿ÐµÑÐµÐ´Ð°ÐµÐ¼ Ð´Ð°Ð½Ð½ÑÐµ Ð² ÑÐ¾Ð²Ð°ÑÑ setGoods
    //     setLoading(false); // Ð·Ð°ÐºÐ°Ð½ÑÐ¸Ð²Ð°ÐµÐ¼ Ð¿ÑÐ¾ÑÐµÐ´ÑÑÑ Ð·Ð°Ð³ÑÑÐ·ÐºÐ¸
    //   });
  }, []);

  return (
  <main className="container content">
    <Cart quantity={order.length} handleBasketShow={handleBasketShow} />
    {
      loading ? <Preloader /> : (
        <GoodsList goods={goods} addToBasket={addToBasket} />
      )}
    {
      isBasketShow && <BasketList order={order} handleBasketShow={handleBasketShow} removeFromBasket={removeFromBasket} incQuantity={incQuantity} decQuantity={decQuantity} />
    }
    {
      alertName && <Alert name={alertName} closeAlert={closeAlert} />
    }
    </main>
  );
};

export default Shop;
